import Component from "../react/component"
import { diff } from "./diff"

const ReactDOM = {
    render
}

function render( vnode, container, dom ){
    return diff( dom, vnode, container )
    // return container.appendChild( _render( vnode ) )
}

function _render( vnode ){
    if( vnode === undefined || vnode === null || typeof vnode === "boolean" ) return

    // æ•°å­—è½¬æ¢æˆ å­—ç¬¦ä¸²
    if( typeof vnode === "number" ) vnode = String( vnode )
    // å¦‚æœ vnode æ˜¯å­—ç¬¦ä¸²ï¼Œç›´æ¥æ¸²æŸ“
    if( typeof vnode === "string" ){
        // åˆ›å»ºæ–‡æœ¬èŠ‚ç‚¹
        return document.createTextNode( vnode )
    }
    // å¦‚æœ tag æ˜¯å‡½æ•°ï¼Œ åˆ™æ¸²æŸ“ç»„ä»¶
    if( typeof vnode.tag === "function" ){
        // 1. åˆ›å»ºç»„ä»¶
        const comp = createComponent( vnode.tag, vnode.attrs )
        // 2. è®¾ç½®ç»„ä»¶çš„å±æ€§
        setComponentProps( comp, vnode.attrs )
        // 3. è¿”å›ç»„ä»¶æ¸²æŸ“çš„èŠ‚ç‚¹å¯¹è±¡
        return comp.base
    }
    // vnode æ˜¯è™šæ‹Ÿ DOM å¯¹è±¡
    const { tag, attrs } = vnode
    // åˆ›å»ºèŠ‚ç‚¹å¯¹è±¡
    const dom = document.createElement( tag )
    // ç»‘å®šå±æ€§
    if( attrs ){
        Object.keys( attrs ).forEach( key => {
            const value = attrs[key]
            setAttribute( dom,  key, value )
        })
    }
    // æ¸²æŸ“å­èŠ‚ç‚¹
    vnode.childrens && vnode.childrens.forEach( child => render( child, dom ) )
    // è¿”å›èŠ‚ç‚¹ å¹¶æŒ‚è½½å­èŠ‚ç‚¹
    return dom
}


function createComponent( comp, props ){
    let instance
    // å¦‚æœæ˜¯ç±»å®šä¹‰åœ¨ç»„ä»¶ï¼Œåˆ™åˆ›å»ºå®ä¾‹  è¿”å›
    if( comp.prototype && comp.prototype.render ){
        instance = new comp( props )
    } else {
        // å‡½æ•°ç»„ä»¶ å°†å‡½æ•°ç»„ä»¶æ‰©å±•æˆ ç±»ç»„ä»¶   æ–¹ä¾¿ç»Ÿä¸€ç®¡ç†
        instance = new Component( props )
        instance.constructor = comp
        // å®šä¹‰ render å‡½æ•°
        instance.render = function(){
            return this.constructor( props )
        }
    }
    return instance
}

// è®¾ç½®ç»„ä»¶å±æ€§
function setComponentProps( comp, props ){
    if( !comp.base ){
        // ç”Ÿå‘½å‘¨æœŸ -------   willMount
        if( comp.componentWillMount ){
            comp.componentWillMount()
        }
    } else if( comp.componentWillReceiveProps ){
        comp.componentWillReceiveProps( props )
    }
    // è®¾ç½®ç»„ä»¶å±æ€§
    comp.props = props
    // æ¸²æŸ“ç»„ä»¶
    renderComponent( comp )
}
// æ¸²æŸ“ç»„ä»¶
export function renderComponent( comp ){
    const renderer = comp.render()
    let base = _render( renderer )
    if( comp.base && comp.componentWillUpdate ){
        comp.componentWillUpdate()
    }
    if( comp.base ){
        if( comp.componentDidUpdate ) comp.componentDidUpdate()
    } else if( comp.componentDidMount ){
        comp.componentDidMount()
    }

    // èŠ‚ç‚¹æ›¿æ¢
    if( comp.base && comp.base.parentNode ){
        comp.base.parentNode.replaceChild( base, comp.base )
    }

    comp.base = base
}

/**
 * è®¾ç½®å±æ€§
 * @param {*} dom 
 * @param {*} key 
 * @param {*} val 
 */
export function setAttribute( dom, key, val ){
    // è®¾ç½® ç±»å
    if( key === "className" ){
        key = "class"
    }
    // äº‹ä»¶   onClick
    if( /on\w+/.test( key ) ){
        // è½¬å°å†™
        key = key.toLowerCase()
        dom[key] = val || ""
    } else if( key === "style" ){
        // æ ·å¼æ˜¯å­—ç¬¦ä¸²
        if( !val || typeof val === "string" ){
            dom.style.cssText = val || ""
        } else {
            // æ ·å¼æ˜¯å¯¹è±¡
            for( let k in val ){
                const { v } = val[ k ]
                if( typeof v === "number" ){
                    dom.style[k] = v + "px"
                } else {
                    dom.style[k] = v
                }
            }
        }
    } else {
        // å…¶ä»–å±æ€§
        if( key in dom ){
            dom[key] = val || ""
        }
        if( val ){
            // æ›´æ–°å€¼
            dom.setAttribute( key, val )
        } else {
            dom.removeAttribute( key )
        }
    }
}


export default ReactDOM
